:root {
  --font-family: "Roboto";
}

body {
  box-sizing: border-box;
  background-color: #d3d3d3;
  flex-direction: column;
  font-family: Arial, Roboto;
  display: flex;
}

#breed-select {
  width: 35%;
  background-color: #938d8d;
  margin: 15px;
  padding: 5px;
}

#breed-select:hover {
  cursor: pointer;
}

.loader, .error {
  background-color: rgba(0, 0, 0, 0);
  margin: 10px 0;
  padding: 10px;
}

.loader {
  color: #2a2828;
  display: flex;
}

.load-section {
  flex-direction: row;
  margin: 15px;
  padding: 5px;
  display: flex;
}

.load-spinner {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 15px;
  padding: 5px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #b71f1f;
}

.cat-info {
  align-items: center;
  display: flex;
}

.cat-info img {
  width: 50%;
  height: auto;
  flex-shrink: 0;
  margin-right: 20px;
}

